<template>
  <v-container
    class="noticias__container p-0 m-0"
    fluid
  >
    <v-container>
      <div class="noticias">
        <div class="content">
          <div
            v-animate-onscroll="{ down: 'animated pulse' }"
            class="title text-h6"
          >
            <h1 class="text-justify">
              Noticias Terapia
            </h1>
            <v-btn
              color="success text-capitalize"
              to="/noticias-psicologia"
            >
              Ver todas
            </v-btn>
          </div>
          <div class="elements">
            <div class="elements-box">
              <card-noticias-home
                v-for="(noticia, index) in getNoticiasPaginadas"
                :key="index"
                v-animate-onscroll="{ down: 'animated pulse' }"
                class="card_not"
                :value="{
                  Titulo: noticia.Titulo,
                  Texto: noticia.Texto,
                  Imagen: noticia.Imagen,
                  Id: noticia.Id,
                  VersionImagen: noticia.VersionImagen,
                }"
              />
            </div>
          </div>
        </div>
      </div>
      <div class="buttons">
        <v-btn
          :disabled="isDisabledBack"
          color="success"
          small
          @click="page--"
        >
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>

        <v-btn
          :disabled="isDisabledForward"
          color="success"
          small
          @click="page++"
        >
          <v-icon>mdi-chevron-right</v-icon>
        </v-btn>
      </div>
    </v-container>
  </v-container>
</template>

<script>
  import { mapState } from 'vuex'

  export default {
    name: 'Noticias',
    components: {
      CardNoticiasHome: () => import('@/components/web/base/CardNoticiasHome'),
    },
    data () {
      return {
        page: 1,
        itemsPerPage: 3,
        backBtnStatus: false,
        forwardBtnStatus: false,
        window: {
          width: '',
        },
        total: '',
      }
    },
    computed: {
      ...mapState('web', ['noticiasHome']),
      isDisabledBack () {
        if (this.page === 1) {
          return true
        }
        return false
      },
      isDisabledForward () {
        if (this.page === this.getTotalPaginas) {
          return true
        }
        return false
      },
      getTotalPaginas () {
        return Math.ceil(this.noticiasHome.length / this.itemsPerPage)
      },

      getNoticiasPaginadas () {
        const start = (this.page - 1) * this.itemsPerPage
        const stop = this.page * this.itemsPerPage

        const noticiasPaginadas = this.noticiasHome.slice(start, stop)
        const noticiasArregladas = []

        Object.keys(noticiasPaginadas).forEach(key => {
          const noticia = {
            Id: noticiasPaginadas[key].Id,
            Titulo: noticiasPaginadas[key].Titulo,
            Texto: noticiasPaginadas[key].Texto,
            Imagen: noticiasPaginadas[key].Imagen,
            Fecha: noticiasPaginadas[key].Fecha,
            Usuario: noticiasPaginadas[key].Usuario,
            VersionImagen: noticiasPaginadas[key].VersionImagen,
          }

          noticiasArregladas.push(noticia)
        })

        return noticiasArregladas
      },
    },
    created () {
      window.addEventListener('resize', this.handleResize)
      this.handleResize()
    // this.printStatus()
    },
    methods: {
      printStatus () {
        console.log(this.noticiasHome)
        console.log(this.forwardBtnStatus)
        console.log(this.page)
        console.log(this.getTotalPaginas)
        console.log(this.getNoticiasPaginadas)
      },
      handleResize () {
        this.window.width = window.innerWidth

        if (window.innerWidth < 800) {
          this.itemsPerPage = 1
        }

        if (window.innerWidth > 800 && window.innerWidth < 1268) {
          this.itemsPerPage = 2
        }

        if (window.innerWidth > 1268) {
          this.itemsPerPage = 3
        }
      },
    },
  }
</script>
